/** Magane plugin **/
div#magane div.stickerWindow {
  background-color: var(--popout-alt) !important;
  border-radius: var(--border-radius-big) !important;
  box-shadow: var(--shadow-3dp) !important;
  right: 256px !important;
  bottom: 66px !important;
  animation: bottom-pop-out 400ms var(--default-animation) !important;
}
div#magane div.stickerWindow div.stickers div.pack span {
  font-weight: bold !important;
  font-variant-caps: small-caps !important;
}
div#magane div.channel-textarea-stickers {
  width: 24px !important;
  position: relative !important;
  right: -6px !important;
}
div#magane .button {
  font-weight: bold !important;
  text-transform: uppercase !important;
}
div#magane .simplebar-scrollbar::before {
  background-color: var(--accent-color) !important;
}

/** PermissionsViewer plugin **/
/* Member Cards */
.member-perms .member-perm {
  position: relative;
  min-width: 16px;
  height: 16px;
  padding: 0;
  border-radius: 16px;
  border: none;
  background: var(--popout-color);
  transition: 200ms ease;
  font-size: 11px;
}
.member-perms .member-perm .perm-circle {
  width: 16px !important;
  height: 16px !important;
}
.member-perms .member-perm .name {
  color: inherit;
  margin: 0 4px 0 2px;
}

